nav{
    background-color: #333;
    overflow: hidden;
    position: sticky;
    top: 0;
    width: 100%;
    z-index: 1000;

}

nav h3{
    display:inline-block;
    color: white;
    text-align: center;
    padding: 14px 16px;
}

nav h3:hover{
    cursor: pointer;
    transform: scale(103%);
}


@media (max-width: 767px) {
    nav h3{
        transform: scale(95%);
        padding: 8px 8px;
    }
}
