.portfolio {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    height: auto;
    overflow-y:auto;
    align-items: center;

    background-image: url("../imgs/background.jpg");
    background-size: cover;
}

.portfolio_card {
    height: auto;
    min-height: 150px;
    width: 80%;
    background-color: pink;
    border-radius: 15px;





    text-overflow: ellipsis;

    transition: opacity 0.3s, transform 0.3s, background-color 0.3s;

    text-align: left;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.portfolio_card:hover {

    transform: scale(102%);
    background-color: lightsalmon;

}



.portfolio_card p {
    padding-right: 3%;
    padding-left: 3%;
}


.portfolioHead:hover{
    transform: scale(105%);
}









