.home{

}


.homeContent {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 70vh;
    padding: 20px;
    overflow-y: auto;
    max-height: 80vh;

    background-image: url("../imgs/background.jpg");
    background-size: cover;

}

.hometext-container {
    flex: 1;
    margin-right: 40px;

    text-align: left;


    background-color: lightsalmon;
    max-height: 100%;

    overflow-y: auto;
    overflow-x: hidden;

    border-radius: 10px;

    transition: opacity 0.3s, transform 0.3s, background-color 0.3s;
}

.hometext-container:hover {
    transform: scale(102%);
}

.hometext_header {
    justify-self: center;
}

.hometext-container p{
    padding-left: 5%;
    padding-right: 5%;
}

.homeimage-container {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

    transition: opacity 0.3s, transform 0.3s, background-color 0.3s;
}

.homeimage-container img {
    max-width: 100%;
    max-height: 400px;
    border-radius: 50%;
}

.homeimage-container:hover {
    transform: scale(102%);
}


/* Mobile responsive media query */
@media (max-width: 767px) {

    .homeContent {
        flex-direction: column;
        justify-content: flex-start; /* Align content to the top */
        height: auto; /* Allow content to determine height */
        max-height: none; /* Remove max-height constraint */
    }

    .hometext-container {
        width: 100%; /* Full width on mobile */
        margin-right: 0; /* Remove right margin */
        margin-top: 20px; /* Add space above the text container */
        height: auto; /* Let content determine height */
        max-height: none; /* Remove max-height constraint */
        overflow-y: visible; /* Allow content to flow naturally */


    }

    .homeimage-container {
        margin-top: 5%;
        width: 80%; /* Full width on mobile */
        height: 80%;
        margin-bottom: 20px; /* Add space below the image container */
    }

}


